
.button {
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  outline: none;
  border: none;
  cursor: pointer;
  -webkit-appearance: none;
  margin: 0;
  font-size: 0;
  white-space: nowrap;
  font-weight: 400;
  display: inline-block;
  vertical-align: middle;
  height: 0.76rem;
  line-height: 0.76rem;
  padding: 0 0.35rem;
  text-align: center;
  border: 1px solid transparent;
  color: #757575;
  background-color: transparent;
  border-radius: 0.06rem;
  -webkit-transition: opacity 0.25s ease-in-out, color 0.25s ease-in-out;
  transition: opacity 0.25s ease-in-out, color 0.25s ease-in-out;
}
.button:hover {
    opacity: 0.85;
}
.button__icon {
    vertical-align: middle;
}
.button__icon + .button__label {
      margin-left: 0.08rem;
}
.button__label {
    font-size: 0.32rem;
    vertical-align: middle;
}
.button__label + .button__icon {
      margin-left: 0.08rem;
}
.button--primary {
    color: #5677fc;
}
.button--secondary {
    color: #e91e63;
}
.button[disabled] {
    cursor: not-allowed;
    opacity: 0.45;
}
.button--type-flat {
    border-color: #f5f5f5;
    background-color: #f5f5f5;
}
.button--type-flat.button--primary {
      color: #ffffff;
      border-color: #5677fc;
      background-color: #5677fc;
}
.button--type-flat.button--secondary {
      color: #ffffff;
      border-color: #e91e63;
      background-color: #e91e63;
}
.button--type-ghost {
    border-color: #bdbdbd;
    background-color: transparent;
}
.button--type-ghost.button--primary {
      border-color: #5677fc;
      background-color: transparent;
}
.button--type-ghost.button--secondary {
      border-color: #e91e63;
      background-color: transparent;
}
.button--full {
    width: 100%;
    padding: 0;
}
.button--size-sm {
    height: 0.56rem;
    line-height: 0.56rem;
    padding: 0 0.25rem;
}
.button--size-sm .button__icon {
      font-size: 0.28rem;
}
.button--size-sm .button__icon + .button__label {
        margin-left: 0.06rem;
}
.button--size-sm .button__label {
      font-size: 0.24rem;
}
.button--size-sm .button__label + .button__icon {
        margin-right: 0.06rem;
}
.button--size-lg {
    height: 1rem;
    line-height: 1rem;
    padding: 0 0.45rem;
}
.button--size-lg .button__icon {
      font-size: 0.4rem;
}
.button--size-lg .button__label {
      font-size: 0.36rem;
}
.button--radius {
    border-radius: 9999px;
}
.button--circle {
    width: 0.76rem;
    padding: 0;
    border-radius: 9999px;
}
.button--circle.button--size-sm {
      width: 0.56rem;
}
.button--circle.button--size-lg {
      width: 1rem;
}



