
.nav-category {
  position: relative;
  color: #ffffff;
}
.nav-category__scroll {
    position: relative;
    height: 1rem;
    background-color: #5677fc;
    padding: 0 1rem 0 0.4rem;
}
.nav-category__scroll:after {
      content: "";
      display: block;
      position: absolute;
      right: 1rem;
      top: 0;
      width: 0.4rem;
      height: 1rem;
      z-index: 1;
      background-image: -webkit-linear-gradient(right, #5677fc 0, transparent 100%);
      background-image: linear-gradient(to left, #5677fc 0, transparent 100%);
}
.nav-category__scroll ul {
      position: relative;
      height: 100%;
      overflow-x: auto;
      white-space: nowrap;
      font-size: 0;
      -webkit-overflow-scrolling: touch;
}
.nav-category__scroll li {
      display: inline-block;
      font-size: 0.32rem;
}
.nav-category__scroll li > a,
      .nav-category__scroll li > div {
        display: block;
        height: 1rem;
        line-height: 1rem;
        padding: 0 0.4rem;
        color: inherit;
        font-size: 0.32rem;
        opacity: 0.6;
        -webkit-transition: opacity 0.2s;
        transition: opacity 0.2s;
        cursor: pointer;
}
.nav-category__scroll li > a.active,
        .nav-category__scroll li > div.active {
          opacity: 1;
}
.nav-category .toggle-all__button {
    position: absolute;
    top: 0.26rem;
    right: 0.3rem;
    z-index: 1050;
    opacity: 0.6;
    -webkit-transition: -webkit-transform .25s;
    transition: -webkit-transform .25s;
    transition: transform .25s;
    transition: transform .25s, -webkit-transform .25s;
    cursor: pointer;
}
.nav-category .toggle-all__button.active {
      -webkit-transform: rotate(-180deg);
              transform: rotate(-180deg);
}
.nav-category__all a {
    color: inherit;
}
.nav-category__all__overlay {
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    z-index: 1000;
    height: 100%;
    width: 100%;
    background-color: rgba(0, 0, 0, 0.5);
    -webkit-transition: opacity .25s;
    transition: opacity .25s;
    opacity: 0;
    visibility: hidden;
}
.nav-category__all.active .nav-category__all__wrapper {
    -webkit-transform: translateY(0);
            transform: translateY(0);
}
.nav-category__all.active .nav-category__all__overlay {
    opacity: 1;
    visibility: visible;
}
.nav-category__all__wrapper {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    z-index: 1000;
    color: #ffffff;
    background-color: #5677fc;
    -webkit-transform: translateY(-100%);
            transform: translateY(-100%);
    -webkit-transition: opacity 0.3s, -webkit-transform 0.45s cubic-bezier(0.23, 1, 0.32, 1);
    transition: opacity 0.3s, -webkit-transform 0.45s cubic-bezier(0.23, 1, 0.32, 1);
    transition: opacity 0.3s, transform 0.45s cubic-bezier(0.23, 1, 0.32, 1);
    transition: opacity 0.3s, transform 0.45s cubic-bezier(0.23, 1, 0.32, 1), -webkit-transform 0.45s cubic-bezier(0.23, 1, 0.32, 1);
}
.nav-category__all__title {
    padding: 0 0.4rem;
    font-size: 0.32rem;
    height: 1rem;
    line-height: 1rem;
    opacity: 0.8;
}
.nav-category__all__content {
    overflow: hidden;
}
.nav-category__all__content li {
      width: 25%;
      float: left;
      margin: 0.16rem 0;
      text-align: center;
}
.nav-category__all__content li > a,
      .nav-category__all__content li > div {
        display: block;
        padding: 0.2rem 0;
        opacity: 0.8;
        cursor: pointer;
}
.nav-category__all__editor {
    border-top: 1px solid rgba(255, 255, 255, 0.8);
    padding: 0.3rem 0;
    text-align: center;
}



