
.list-menu__item {
  position: relative;
  overflow: hidden;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-item-align: stretch;
      align-self: stretch;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  color: inherit;
}
.list-menu__item:hover {
    color: inherit;
}
.list-menu__item:active {
    background-color: #fafafa;
}
.list-menu__item[disabled="disabled"] .list-menu__item__thumb,
  .list-menu__item[disabled="disabled"] .list-menu__item__title,
  .list-menu__item[disabled="disabled"] .list-menu__item__extra,
  .list-menu__item[disabled="disabled"] .list-menu__item__arrow {
    opacity: 0.4;
}
.list-menu__item[disabled="disabled"]:active {
    background-color: transparent;
}
.list-menu__item.list-menu__item__line:after {
    content: "";
    position: absolute;
    left: 0;
    right: 0;
    bottom: 0;
    border-bottom: 1px solid #e0e0e0;
    display: none;
}
.list-menu__item .list-menu__item__thumb {
    padding-left: 0.3rem;
}
.list-menu__item .list-menu__item__thumb .icon {
      font-size: 1.2em;
}
.list-menu__item .list-menu__item__thumb img {
      display: block;
      width: 0.48rem;
      height: 0.48rem;
      vertical-align: middle;
}
.list-menu__item .list-menu__item__content {
    position: relative;
    min-height: 1.2rem;
    padding: 0.2rem 0;
    margin-left: 0.3rem;
    -webkit-box-flex: 1;
        -ms-flex: 1;
            flex: 1;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-item-align: stretch;
        align-self: stretch;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    max-height: 9999px;
}
.list-menu__item .list-menu__item__content .list-menu__item__title {
      -webkit-box-flex: 1;
          -ms-flex: 1;
              flex: 1;
      line-height: 1.4;
}
.list-menu__item .list-menu__item__content .list-menu__item__title span {
        vertical-align: middle;
}
.list-menu__item .list-menu__item__content .list-menu__item__brief {
      color: rgba(0, 0, 0, 0.5);
      font-size: 0.8em;
}
.list-menu__item .list-menu__item__extra {
    color: rgba(0, 0, 0, 0.5);
    font-size: 0.8em;
    text-align: right;
    padding: 0 0.2rem 0 0.3rem;
}
.list-menu__item .list-menu__item__extra span {
      vertical-align: middle;
}
.list-menu__item .list-menu__item__arrow {
    padding-right: 0.3rem;
    color: rgba(0, 0, 0, 0.3);
    font-size: 0.8em;
}



