
.toast__overlay {
  height: 100%;
  background-color: #000;
  opacity: 0;
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1000;
}
.toast__content {
  position: fixed;
  max-width: 80%;
  padding: 0.28rem 0.42rem;
  border-radius: 0.12rem;
  background: rgba(0, 0, 0, 0.7);
  color: #fff;
  text-align: center;
  z-index: 1000;
}
.toast[data-type="success"] .toast__content, .toast[data-type="fail"] .toast__content, .toast[data-type="offline"] .toast__content, .toast[data-type="loading"] .toast__content {
  border-radius: 0.2rem;
  min-width: 2.24rem;
  min-height: 2.24rem;
}
.toast__middle {
  left: 50%;
  top: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}
.toast__bottom {
  left: 50%;
  bottom: 0.6rem;
  -webkit-transform: translate(-50%, 0);
          transform: translate(-50%, 0);
}
.toast__icon {
  display: block;
  text-align: center;
  font-size: 0.9rem;
  width: 0.98rem;
  height: 0.98rem;
  margin: 0.02rem auto 0.28rem;
}
.toast__icon.animate-loading {
    width: 0.82rem;
    height: 0.82rem;
    margin: 0.08rem auto 0.38rem;
    display: inline-block;
    vertical-align: middle;
    border: 0.04rem solid #fff;
    border-bottom-color: transparent;
    border-radius: 50%;
    -webkit-animation: loading 0.8s infinite linear;
            animation: loading 0.8s infinite linear;
}
.toast__text {
  font-size: 0.28rem;
  display: block;
  text-align: center;
  line-height: 1;
  padding: 0.06rem 0;
}
.toast-enter,
.toast-enter-active,
.toast-leave,
.toast-leave-active {
  -webkit-transition: all .15s linear;
  transition: all .15s linear;
}
.toast-enter-active,
.toast-leave {
  opacity: 1;
}
.toast-enter,
.toast-leave-active {
  opacity: 0;
}
.toast__bottom.toast-enter-active {
  -webkit-animation: slideToast .8s 1 linear;
          animation: slideToast .8s 1 linear;
}
.overlay-fade-enter-active,
.overlay-fade-leave-active {
  -webkit-transition: opacity 0.2s cubic-bezier(0.23, 1, 0.32, 1);
  transition: opacity 0.2s cubic-bezier(0.23, 1, 0.32, 1);
}
.overlay-fade-enter,
.overlay-fade-leave-active {
  opacity: 0 !important;
}
@-webkit-keyframes slideToast {
0% {
    bottom: 0;
    opacity: 0;
}
20% {
    bottom: 0.8rem;
    opacity: 1;
}
40% {
    bottom: 0.6rem;
    opacity: 1;
}
100% {
    bottom: 0.6rem;
    opacity: 1;
}
}
@keyframes slideToast {
0% {
    bottom: 0;
    opacity: 0;
}
20% {
    bottom: 0.8rem;
    opacity: 1;
}
40% {
    bottom: 0.6rem;
    opacity: 1;
}
100% {
    bottom: 0.6rem;
    opacity: 1;
}
}
@-webkit-keyframes loading {
0% {
    -webkit-transform: rotate3d(0, 0, 1, 0deg);
            transform: rotate3d(0, 0, 1, 0deg);
}
100% {
    -webkit-transform: rotate3d(0, 0, 1, 360deg);
            transform: rotate3d(0, 0, 1, 360deg);
}
}
@keyframes loading {
0% {
    -webkit-transform: rotate3d(0, 0, 1, 0deg);
            transform: rotate3d(0, 0, 1, 0deg);
}
100% {
    -webkit-transform: rotate3d(0, 0, 1, 360deg);
            transform: rotate3d(0, 0, 1, 360deg);
}
}



