
.grid__item {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  text-align: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  position: relative;
  padding: 0.4rem 0.2rem;
  cursor: pointer;
}
.grid__item.grid__item--line:after {
    content: "";
    position: absolute;
    top: 0.4rem;
    bottom: 0.4rem;
    right: 0;
    width: 1px;
    background-color: #e0e0e0;
}
.grid__item.grid__item--line.grid__item--line:last-child:after {
    display: none;
}
.grid__item .grid__icon {
    margin-bottom: 0.3rem;
}
.grid__item .grid__text {
    color: rgba(0, 0, 0, 0.8);
}



