
.user-header {
  padding: 0.8rem 0.4rem;
  text-align: center;
  color: #ffffff;
  background-color: #5677fc;
}
.user-header a {
    display: block;
    color: #ffffff;
}
.user-header .user-avatar {
    width: 1.5rem;
    height: 1.5rem;
    margin: 0 auto;
}
.user-header .user-avatar img {
      display: block;
      width: 100%;
      height: 100%;
      border-radius: 50%;
}
.user-header .user-name {
    margin-top: 0.3rem;
}
.common-operate .grid__icon.cyan {
  color: #1ca78e;
}
.common-operate .grid__icon.blue-light {
  color: #8fa2ef;
}
.common-operate .grid__icon.orange {
  color: #ff6600;
}



