
.comment {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  padding-top: 0.4rem;
}
.comment:last-child .comment__info {
    border-bottom: none;
}
.comment__avatar {
    width: 0.6rem;
    height: 0.6rem;
    margin-right: 0.2rem;
}
.comment__avatar img {
      width: 100%;
      height: 100%;
      display: block;
      border-radius: 50%;
      background-color: rgba(0, 0, 0, 0.1);
}
.comment__info {
    -webkit-box-flex: 1;
        -ms-flex: 1;
            flex: 1;
    border-bottom: 1px solid #e5e5e5;
    padding-bottom: 0.4rem;
    min-width: 0;
}
.comment__media {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    -webkit-box-pack: justify;
        -ms-flex-pack: justify;
            justify-content: space-between;
    background-color: transparent;
}
.comment__actions {
    font-size: 0;
    vertical-align: middle;
}
.comment__actions .iconfont {
      font-size: 0.3rem;
      margin-right: 0.1rem;
}
.comment__actions span {
      font-size: 0.28rem;
      margin-left: 0.3rem;
      opacity: 0.6;
      cursor: pointer;
}
.comment__actions span.praised {
        color: #5677fc;
        opacity: 1;
}
.comment__actions span em {
        vertical-align: middle;
}
.comment__username {
    font-size: 0.28rem;
    margin-bottom: 0.12rem;
}
.comment__date {
    font-size: 0.22rem;
    opacity: 0.5;
}
.comment__content {
    margin-top: 0.3rem;
    font-size: 0.3rem;
    line-height: 1.4;
    text-align: justify;
    max-height: 9999px;
    word-wrap: break-word;
}
.comment__content p {
      margin-bottom: 0.2rem;
      word-wrap: break-word;
}
.comment__content p:last-child {
        margin-bottom: 0;
}



