
.divider {
  position: relative;
  border-top: 1px solid #e0e0e0;
  text-align: center;
  color: #bdbdbd;
}
.divider__label {
    position: absolute;
    top: -0.5em;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    padding: 0 1em;
    background-color: #ffffff;
    color: #bdbdbd;
}
.divider--space-sm {
    margin-top: 0.2rem;
    margin-bottom: 0.2rem;
}
.divider--space-md {
    margin-top: 0.5rem;
    margin-bottom: 0.5rem;
}
.divider--space-lg {
    margin-top: 0.8rem;
    margin-bottom: 0.8rem;
}



