
.button-group {
  font-size: 0;
}
.button-group .button {
    margin-right: 0.2rem;
}
.button-group .button:last-child {
      margin-right: 0;
}
.button-group--align-center {
    text-align: center;
}
.button-group--align-center .button {
      margin-left: 0.1rem;
      margin-right: 0.1rem;
}
.button-group--align-center .button:first-child {
        margin-left: 0;
}
.button-group--align-center .button:last-child {
        margin-right: 0;
}
.button-group--align-right {
    text-align: right;
}
.button-group--align-right .button {
      margin-left: 0.2rem;
}
.button-group--align-right .button:first-child {
        margin-right: 0;
        margin-left: 0;
}
.button-group--collapse .button {
    margin-left: 0;
    margin-right: 0;
}
.button-group--size-sm .button {
    height: 0.56rem;
    line-height: 0.56rem;
    padding: 0 0.25rem;
}
.button-group--size-sm .button .button__icon {
      font-size: 0.28rem;
}
.button-group--size-sm .button .button__icon + .button__label {
        margin-left: 0.06rem;
}
.button-group--size-sm .button .button__label {
      font-size: 0.24rem;
}
.button-group--size-sm .button .button__label + .button__icon {
        margin-right: 0.06rem;
}
.button-group--size-lg .button {
    height: 1rem;
    line-height: 1rem;
    padding: 0 0.45rem;
}
.button-group--size-lg .button .button__icon {
      font-size: 0.4rem;
}
.button-group--size-lg .button .button__label {
      font-size: 0.36rem;
}



