
.panel-simple {
  margin: 0.2rem 0;
}
.panel-simple--top--no {
    margin-top: 0;
}
.panel-simple--bottom--no {
    margin-bottom: 0;
}
.panel-simple.panel-simple--blank .panel-simple__body {
    padding-left: 0.3rem;
    padding-right: 0.3rem;
}
.panel-simple.panel-simple--space .panel-simple__body {
    padding-top: 0.3rem;
    padding-bottom: 0.3rem;
}
.panel-simple.panel-simple--inside .panel-simple__header {
    background-color: #ffffff;
    overflow: hidden;
    padding-top: 0.3rem;
}
.panel-simple .panel-simple__header {
    color: #9e9e9e;
    font-size: 0.24rem;
    padding-bottom: 0.2rem;
    padding-left: 0.3rem;
    padding-right: 0.3rem;
    max-height: 9999px;
}
.panel-simple .panel-simple__body {
    background-color: #ffffff;
}



