
.text-field {
  margin-bottom: 0.6rem;
  position: relative;
  color: rgba(0, 0, 0, 0.4);
}
.text-field__label--has {
    padding-top: 0.3rem;
}
.text-field.state--focus {
    color: #5677fc;
}
.text-field.state--focus .text-field__content {
      border-color: #5677fc;
}
.text-field.state--focus .text-field__content:after {
        border-color: #5677fc;
}
.text-field.state--error {
    color: #f44336;
}
.text-field.state--error .text-field__content {
      border-color: #f44336;
}
.text-field.state--error .text-field__content:after {
        border-color: #f44336;
}
.text-field.state--disabled {
    color: rgba(0, 0, 0, 0.2);
}
.text-field.state--disabled .text-field__hint {
      color: inherit;
}
.text-field.state--disabled .text-field__input {
      color: inherit;
      cursor: not-allowed;
}
.text-field.state--disabled .text-field__content {
      border-bottom: 1px dotted transparent;
}
.text-field.state--disabled .text-field__content:after {
        border-bottom: 2px dotted rgba(0, 0, 0, 0.2);
}
.text-field__content {
  position: relative;
  padding-top: 0.04rem;
  border-bottom: 1px solid rgba(0, 0, 0, 0.2);
  -webkit-transition: border .25s;
  transition: border .25s;
}
.text-field__content:after {
    content: "";
    display: block;
    position: absolute;
    left: 0;
    right: 0;
    bottom: 0;
    border-bottom: 1px solid transparent;
    -webkit-transition: border .25s;
    transition: border .25s;
}
.text-field__input {
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
  outline: none;
  border: none;
  background: none;
  border-radius: 0 0 0 0;
  box-shadow: none;
  padding: 0;
  margin: 0;
  position: relative;
  display: block;
  width: 100%;
  height: 0.64rem;
  color: rgba(0, 0, 0, 0.87);
}
.text-field__textarea {
  resize: none;
  min-height: 1.6rem;
  max-height: 3.6rem;
  line-height: 1.4;
}
.text-field__help {
  position: absolute;
  left: 0;
  right: 0;
  bottom: -0.36rem;
  width: 100%;
  font-size: 0.24rem;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
}



