
.text-field__label {
  position: absolute;
  top: 0.5rem;
  color: inherit;
  font-size: 0.28rem;
  -webkit-transform-origin: left top;
          transform-origin: left top;
  -webkit-transition: all 0.45s cubic-bezier(0.23, 1, 0.32, 1);
  transition: all 0.45s cubic-bezier(0.23, 1, 0.32, 1);
}
.text-field__label.label--float {
    font-size: 0.24rem;
    top: 0rem;
}



