
.share-grids {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}
.share-grids .share-item {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    width: 25%;
    height: calc(100vw/4);
    max-height: 4rem;
    text-align: center;
    cursor: pointer;
    border-right: 1px solid #eeeeee;
    border-top: 1px solid #eeeeee;
}
.share-grids .share-item:hover:not([data-empty]), .share-grids .share-item:active:not([data-empty]) {
      background-color: #f5f5f5;
}
.share-grids .share-item:nth-of-type(1), .share-grids .share-item:nth-of-type(2), .share-grids .share-item:nth-of-type(3), .share-grids .share-item:nth-of-type(4) {
      border-top: none;
}
.share-grids .share-item:nth-of-type(4n+4) {
      border-right: none;
}
.share-grids .share__social__name {
    margin-top: 0.2rem;
    font-size: 0.28rem;
    color: #9e9e9e;
}
.share__copytip {
  padding: 0.4rem;
  font-size: 0.32rem;
}



