
.grids {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
.grids--line {
  border-bottom: 1px solid #e0e0e0;
}
.grids--line:last-child {
    border-bottom: none;
}
.grids--line .grid__item {
    border-right: 1px solid #e0e0e0;
}
.grids--line .grid__item:last-child {
      border-right: none;
}



