
.badge {
  position: relative;
  display: inline-block;
  line-height: 1;
  vertical-align: middle;
}
.badge sup {
    top: 0;
    display: inline-block;
    margin: 0 0.1rem;
    color: #ffffff;
    background-color: #ff5722;
    border-radius: 0.36rem;
    vertical-align: middle;
    white-space: nowrap;
    box-shadow: 0 0 0 1px #ffffff;
}
.badge .badge-count {
    padding: 0 0.1rem;
    min-width: 0.36rem;
    height: 0.36rem;
    line-height: 0.36rem;
    text-align: center;
    font-size: 0.24rem;
}
.badge .badge-dot {
    top: -0.1em;
    display: inline-block;
    height: 0.18rem;
    width: 0.18rem;
    line-height: 1;
    padding: 0;
    border-radius: 50%;
}
.badge.badge-radius .badge-count {
    border-radius: 0.04rem;
}
.badge-primary .badge-count, .badge-primary .badge-dot {
  background-color: #5677fc;
}
.badge-secondary .badge-count, .badge-secondary .badge-dot {
  background-color: #e91e63;
}
.badge-transparent .badge-count, .badge-transparent .badge-dot {
  background-color: transparent;
  color: #ff5722;
  border: 1px solid #ff5722;
}
.badge-transparent.badge-primary .badge-count, .badge-transparent.badge-primary .badge-dot {
  color: #5677fc;
  border: 1px solid #5677fc;
}
.badge-transparent.badge-secondary .badge-count, .badge-transparent.badge-secondary .badge-dot {
  color: #e91e63;
  border: 1px solid #e91e63;
}
.badge-float .badge-count, .badge-float .badge-dot {
  margin: 0;
  position: absolute;
  z-index: 10;
}
.badge-float .badge-count {
  top: -0.18rem;
  right: -0.18rem;
}
.badge-float .badge-dot {
  top: -0.09rem;
  right: -0.09rem;
}



