
.list-menu--line .list-menu__item__content:after {
  content: "";
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
  border-bottom: 1px solid #e0e0e0;
}
.list-menu--line .list-menu__item:last-child .list-menu__item__content:after {
  display: none;
}
.list-menu--line__full .list-menu__item:after {
  content: "";
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
  border-bottom: 1px solid #e0e0e0;
}
.list-menu--line__full .list-menu__item:last-child:after {
  display: none;
}



