
.checkbox {
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  cursor: pointer;
  position: relative;
  display: inline-block;
}
.checkbox input[type=checkbox] {
    display: none;
}
.checkbox input[type=checkbox]:checked + .checkbox-wrapper .checkbox__icon--uncheck {
      opacity: 0;
}
.checkbox input[type=checkbox]:checked + .checkbox-wrapper .checkbox__icon--check {
      opacity: 1;
}
.checkbox.disabled {
    cursor: not-allowed;
}
.checkbox.disabled .checkbox__icon--uncheck, .checkbox.disabled .checkbox__icon--check, .checkbox.disabled .checkbox__label {
      color: rgba(0, 0, 0, 0.4);
}
.checkbox-wrapper {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
}
.checkbox__icon {
  width: 0.48rem;
  height: 0.48rem;
  vertical-align: middle;
  position: relative;
  margin-right: 0.2rem;
}
.checkbox__icon--right {
    margin-right: 0;
    margin-left: 0.2rem;
}
.checkbox__icon--uncheck, .checkbox__icon--check {
    position: absolute;
    top: 0;
    left: 0;
}
.checkbox__icon--uncheck {
    opacity: 1;
    -webkit-transition: opacity 0.35s cubic-bezier(0.23, 1, 0.32, 1);
    transition: opacity 0.35s cubic-bezier(0.23, 1, 0.32, 1);
}
.checkbox__icon--check {
    color: #5677fc;
    opacity: 0;
    -webkit-transition: opacity 0.35s cubic-bezier(0.23, 1, 0.32, 1);
    transition: opacity 0.35s cubic-bezier(0.23, 1, 0.32, 1);
}



