
.topic-item {
  margin-bottom: 0.3rem;
}
.topic-item a {
    display: block;
    color: inherit;
    position: relative;
    background-color: #ffffff;
    padding: 0.3rem;
}
.topic-item .icon-top,
  .topic-item .icon-good {
    position: absolute;
    top: 0;
    display: block;
    width: 0.5rem;
    height: 0.94rem;
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
}
.topic-item .icon-top {
    right: 0.3rem;
    background-image: url("../../assets/icon-top.png");
}
.topic-item .icon-good {
    right: 1rem;
    background-image: url("../../assets/icon-good.png");
}
.topic-item__extra {
    margin-bottom: 0.4rem;
    background-color: transparent;
}
.topic-item__avatar {
    width: 0.7rem;
    height: 0.7rem;
    margin-right: 0.2rem;
}
.topic-item__avatar img {
      width: 100%;
      height: 100%;
      border-radius: 50%;
}
.topic-item__author {
    font-size: 0.28rem;
    color: #424242;
}
.topic-item__create {
    margin-top: 0.14rem;
    font-size: 0.24rem;
    color: #bdbdbd;
}
.topic-item__title {
    font-size: 0.38rem;
    margin-bottom: 0.3rem;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
}
.topic-item__summary {
    font-size: 0.3rem;
    line-height: 1.2;
    margin-bottom: 0.5rem;
    color: #9e9e9e;
    max-height: 99999px;
    overflow: hidden;
    word-break: break-all;
    display: -webkit-box;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
}
.topic-item__media {
    border-top: 1px solid #e0e0e0;
    padding: 0.3rem 0 0;
    font-size: 0.26rem;
    color: #bdbdbd;
    max-height: 99999px;
}
.topic-item__media__left span {
      margin-right: 0.2rem;
}
.topic-item__media .iconfont {
      font-size: 0.3rem;
}



