
.message {
  position: relative;
  background-color: #fff;
  padding: 0.25rem 0.25rem 0;
  margin-bottom: 0.2rem;
  margin-left: 1rem;
}
.message:before {
    content: " ";
    position: absolute;
    left: -0.72rem;
    top: 0.5rem;
    bottom: -0.8rem;
    z-index: 1;
    display: block;
    width: 0.04rem;
    background: #e0e0e0;
}
.message:last-child:before {
    display: none;
}
.message__avatar {
    width: 0.6rem;
    height: 0.6rem;
    position: absolute;
    z-index: 11;
    left: -1rem;
    top: 0.25rem;
}
.message__avatar img {
      position: relative;
      z-index: 11;
      width: 100%;
      height: 100%;
      display: block;
      border-radius: 50%;
      background-color: #eeeeee;
}
.message__info {
    -webkit-box-flex: 1;
        -ms-flex: 1;
            flex: 1;
    position: relative;
}
.message__info:after {
      content: " ";
      height: 0;
      width: 0;
      position: absolute;
      top: 0.1rem;
      left: -0.65rem;
      pointer-events: none;
      border: solid transparent;
      border-right-color: #fff;
      border-width: 0.2rem;
}
.message__media {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    -webkit-box-pack: justify;
        -ms-flex-pack: justify;
            justify-content: space-between;
    background-color: transparent;
    padding: 0.25rem 0;
    font-size: 0.24rem;
    border-top: 1px solid #e5e5e5;
}
.message__media__right {
      opacity: 0.6;
}
.message__media .sep {
      margin: 0 0.14rem;
}
.message__title {
    font-size: 0.28rem;
    opacity: 0.8;
    max-height: 9999px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
}
.message__content {
    margin: 0.3rem 0 0.4rem;
    font-size: 0.3rem;
    line-height: 1.4;
    text-align: justify;
    max-height: 9999px;
    word-break: break-all;
}



