
.state {
  text-align: center;
  padding-top: 0.2rem;
  padding-bottom: 0.2rem;
}
.state__image {
    opacity: 0.5;
    font-size: 0;
}
.state__image + .state__text {
      margin-top: 0.3rem;
}
.state__text {
    opacity: 0.5;
}
.state__text + .state__actions {
      margin-top: 0.6rem;
}
.state--style-middle {
    position: fixed;
    left: 50%;
    top: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    width: 100%;
}
.state--style-middle .state__content {
      max-width: 90%;
      margin: 0 auto;
}
.state--style-line {
    padding-top: 0.4rem;
    padding-bottom: 0.4rem;
}
.state--size-sm .state__image img {
    max-width: 0.8rem;
    max-height: 0.8rem;
}
.state--size-sm .state__text {
    font-size: 0.28rem;
}
.state--size-md .state__image img {
    max-width: 1.6rem;
    max-height: 1.6rem;
}
.state--size-md .state__text {
    font-size: 0.32rem;
}
.state--size-lg .state__image img {
    max-width: 3.2rem;
    max-height: 3.2rem;
}
.state--size-lg .state__text {
    font-size: 0.36rem;
}



