
.media-box {
  margin-bottom: 0.3rem;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  color: inherit;
  position: relative;
  background-color: #ffffff;
  padding: 0.3rem;
}
.media-box__body {
    -webkit-box-flex: 1;
        -ms-flex: 1;
            flex: 1;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
    -webkit-box-pack: justify;
        -ms-flex-pack: justify;
            justify-content: space-between;
    min-width: 0;
}
.media-box__header {
    width: 0.86rem;
    height: 0.86rem;
    margin-right: 0.3rem;
}
.media-box__header img {
      width: 100%;
      height: 100%;
      border-radius: 50%;
}
.media-box__footer {
    position: relative;
    right: -0.1rem;
    color: #bdbdbd;
}
.media-box__title {
    font-size: 0.32rem;
    margin-bottom: 0.2rem;
    overflow: hidden;
    word-break: break-all;
    display: -webkit-box;
    -webkit-line-clamp: 1;
    -webkit-box-orient: vertical;
}
.media-box__meta {
    font-size: 0.26rem;
    color: #bdbdbd;
    max-height: 99999px;
}
.media-box__meta em {
      margin: 0 0.02rem;
}



