
.text-field__hint {
  position: absolute;
  font-size: 0.28rem;
  line-height: 0.62rem;
  opacity: 0;
  -webkit-transition: opacity 0.45s cubic-bezier(0.23, 1, 0.32, 1);
  transition: opacity 0.45s cubic-bezier(0.23, 1, 0.32, 1);
  color: rgba(0, 0, 0, 0.4);
}
.text-field__hint.show {
    opacity: 1;
}



