
.page-about__content {
  padding: 0.3rem;
  line-height: 1.5;
  max-height: 999999px;
}
.page-about__content h3 {
    font-size: 0.36rem;
    margin-top: 0.5rem;
    margin-bottom: 0.3rem;
    color: #000000;
}
.page-about__content h3:first-child {
      margin-top: 0;
}
.page-about__content p {
    margin-bottom: 0.3rem;
}
.page-about__content p :last-child {
      margin-bottom: 0;
}
.page-about__content .logo__cnode {
    width: 6.4rem;
    max-width: 80%;
    display: block;
    margin: 0 auto 0.8rem;
}



